import React from 'react';
import {Link} from 'react-router-dom';
import allBlogs from "../blogdata";

type blog = {
    id : string,
    name : string
}

function Header() {
    return (
      <div className="header">
        <Link className="header-item" to="/">Tom Davenport</Link>
        <Link className="header-item" to="/">Blog List</Link>
        <select className="header-item">
            {allBlogs.map( (blog : blog) => <option value={blog.id}>{blog.name}</option> )}
        </select>
      </div>
    );
  }
  
  export default Header;