import React from 'react';
import BlogItem from "./BlogItem";
import allBlogs from "../blogdata";

type blog = {
    id : string,
    name : string
}

function BlogList() {
    return <div className="blog-list">
        <div className="about-message">
          <p>This is my quick portfolio site, mostly to practise my react.</p>
          <p>Click the links below to see some data science examples.</p>
          <p>Drop me a message on github to chat.</p>
        </div>
        { allBlogs.map( (blog) => <BlogItem id={blog.id} name={blog.name}  />  )}
      </div>
    
  }
  
  export default BlogList;